import {useNavigate} from 'react-router-dom';
import cn from 'classnames';
import {Button} from '@ya.praktikum/react-developer-burger-ui-components';
import notFoundStyles from './not-found.module.css';

export const NotFound404 = () => {

  const navigate = useNavigate();

  function goHome() {
    navigate('/');
  }

  function goBack() {
    navigate(-1);
  }

  return (
      <section className={notFoundStyles.page}>
        <p className='text text_type_digits-large text_color_inactive'>404</p>
        <h2 className='text text_type_main-large text_color_inactive'>  </h2>
        <div className={cn(notFoundStyles.container, 'mt-20')}>
          <span className='text text_type_main-default text_color_active'>
             ,       .
          </span>
          <span className='text text_type_main-default text_color_active'>
                , 
            <Button extraClass={cn(notFoundStyles.button, 'ml-2 mr-2')}
                    htmlType='button'
                    type='secondary'
                    size='medium'
                    onClick={goBack}>
              
            </Button>
              
            <Button extraClass={cn(notFoundStyles.button, 'ml-2')}
                    htmlType='button'
                    type='secondary'
                    size='medium'
                    onClick={goHome}>
               
            </Button>.
          </span>
        </div>
      </section>
  );
}
